import { Component, Input } from '@angular/core';
import { FirewallCountsInterceptor } from 'app/interceptors/firewall-counts.interceptor';

@Component({
    selector: 'i360-list-count',
    template: `
        <span class="title">
            {{ 'lists.lists.search.' + item | translate }}
            <i [matTooltip]="'lists.lists.searchHints.' + item | translate"
               class="material-icons">help</i>
        </span>
        <span>
            {{ 'lists.lists.search.server' | translate }}:
            <span class="i360-search-value">{{firewallService.counts?.server[item]}}</span>
        </span>
        <span>
            {{ 'lists.lists.search.cloud' | translate }}:
            <span class="i360-search-value">{{firewallService.counts?.cloud[item]}}</span>
        </span>
    `,
    styleUrls: ['./list-count.component.scss'],
})
export class ListCountComponent {
    @Input() item: string;
    constructor(
        public firewallService: FirewallCountsInterceptor
    ) {}
}
