import { Component, ViewChild } from '@angular/core';
import { BlockedPortsService } from 'app/services/blocked-ports';
import { useLoader } from 'app/utils/loader/decorators';
import {
    AddIpToPortModalComponent,
} from 'app/components/blocked-ports/add-ip-modal/add-ip-to-port-modal.component';
import { NotificationsService } from 'app/services/notifications';
import { BlockedPort, BlockedPortIp } from '@imunify360-api/blocked-ports';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { I360Validators } from 'app/utils/validator/validators';
import { AddItemFormComponent } from 'app/utils/add-item/add-item-form/add-item-form.component';
import { AppState } from 'app/core/app.service';
import { ModalService } from 'app/utils/modals/modal.service';
import { TranslateService } from 'app/utils/translate/translate.service';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { TableComponent } from 'app/utils/table/component/table.component';


@Component({
    selector: 'i360-blocked-ports',
    templateUrl: './blocked-ports.component.html',
    styleUrls: ['./blocked-ports.component.scss'],
})
export class BlockedPortsComponent {
    TCP = 'tcp';
    UDP = 'udp';
    ALL = 'all';
    ipValidation = I360Validators.partOfIP;
    ipVisibleLimit = 10;
    form: UntypedFormGroup;
    @ViewChild(AddItemFormComponent, {static: true}) addItemForm: AddItemFormComponent;
    @ViewChild(RequestDirective, {static: true}) request: RequestDirective;
    @ViewChild(TableComponent, {static: true}) table: TableComponent;
    constructor(public blockedPortsService: BlockedPortsService,
                public notifications: NotificationsService,
                public appState: AppState,
                private modalService: ModalService,
                private translate: TranslateService,
                formBuilder: UntypedFormBuilder) {
        this.form = formBuilder.group({
            port: formBuilder.control('',
                [I360Validators.required('port'), I360Validators.integer(false),
                    Validators.min(0), Validators.max(65535)]),
            protocols: formBuilder.control([this.TCP], [I360Validators.required('protocol')]),
            comment: formBuilder.control(''),
            ips: formBuilder.control('', [I360Validators.listOfIPWithCIDR]),
        });
    }

    async addPort() {
        const {ips, port, protocols, comment} = this.form.value;
        const aPort = new BlockedPort({
            ips: ips.split(',')
                .map((ip: string) => ip.trim())
                .filter((ip: string) => !!ip)
                .map((ip: string) => ({ip})),
            port,
            proto: protocols.length === 2 ? this.ALL : protocols[0],
            comment,
        });
        await this.performAddPort(aPort);
        this.notifications.success(
            'blockedPorts.notifications.portAdded', null, {port, ips, count: ips.length},
        );
        this.form.reset();
        this.addItemForm.addingItem = false;
        await this.request.load();
    }

    @useLoader
    private async performAddPort(aPort: BlockedPort): Promise<void> {
        await this.blockedPortsService.addPort(aPort);
    }

    async deletePort(port: BlockedPort) {
        const confirm = await this.translate.t('blockedPorts.notifications.portDeleteConfirm', {
            port, count: port.ips.length});
        if (await this.modalService.confirm('delete-port', [
                confirm,
                ...port.ips.map(ip => ip.ip),
            ])) {
            await this.performDeletePort(port);
            this.notifications.success(
                'blockedPorts.notifications.portDeleted', null, {port},
            );
            await this.request.load();
        }
    }

    @useLoader
    private async performDeletePort(port: BlockedPort): Promise<void> {
        await this.blockedPortsService.deletePort(port);
    }

    async deleteIp(port: BlockedPort, ip: BlockedPortIp) {
        const confirm = await this.translate.t('blockedPorts.notifications.ipDeleteConfirm',
            {port, ip});
        if (await this.modalService.confirm('delete-ip-from-port', [confirm])) {
            await this.performDeleteIp(port, ip);
            this.notifications.success(
                'blockedPorts.notifications.ipDeleted', null, {port, ip},
            );
            await this.request.load();
        }
    }

    @useLoader
    private async performDeleteIp(port: BlockedPort, ip: BlockedPortIp): Promise<void> {
        await this.blockedPortsService.deleteIp({port, ip: ip.ip});
    }

    async addIp(port: BlockedPort) {
        const modalResult = await this.modalService.open(AddIpToPortModalComponent);
        if (modalResult !== false) {
            let {ip, comment} = modalResult;
            await this.performAddIp(port, ip, comment);
            this.notifications.success(
                'blockedPorts.notifications.ipAdded', null, {port, ip},
            );
            await this.request.load();
        }
    }

    @useLoader
    private async performAddIp(port: BlockedPort, ip: string, comment: string): Promise<void> {
        await this.blockedPortsService.addIp({port, ip, comment});
    }

    @useLoader
    async editPortComment(port: BlockedPort, comment: string) {
        await this.blockedPortsService.editPortComment({port, comment});
        this.notifications.success(
            'blockedPorts.notifications.portCommentEdited', null, {port},
        );
        port.comment = comment;
        this.table.detector.markForCheck();
    }

    @useLoader
    async editIpComment(port: BlockedPort, ip: BlockedPortIp, comment: string) {
        await this.blockedPortsService.editIpComment({port, ip: ip.ip, comment});
        this.notifications.success(
            'blockedPorts.notifications.ipPortCommentEdited', null, {port, ip},
        );
        ip.comment = comment;
        this.table.detector.markForCheck();
    }
}
