import { ModalEntry } from 'app/utils/modals/entry';
import { Component, Inject } from '@angular/core';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AdvicesListsType } from '@imunify360-api/advisor';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
    selector: 'advisor-modal',
    templateUrl: './advisor-modal.component.html',
    styleUrls: ['./advisor-modal.component.scss'],
})
export class AdvisorModalComponent extends ModalEntry<AdvicesListsType, boolean[]> {
    form = this.formBuilder.array(
        this.data.map(() =>  this.formBuilder.control(true)),
    );
    constructor(
        private dialogRef: MatDialogRef<AdvisorModalComponent>,
        private formBuilder: I360FormBuilder,
        @Inject(MAT_DIALOG_DATA) public data: AdvicesListsType,
    ) {
        super();
        dialogRef.disableClose = true;
    }
}
