import { Component, Input } from '@angular/core';
import { FirewallCountsInterceptor } from 'app/interceptors/firewall-counts.interceptor';


@Component({
    selector: 'i360-list-counts',
    template: `
        <div class="i360-search-result">
            <div class="i360-search-title" *ngIf="widgetsHasParams">{{ 'lists.lists.search.blockTitle' | translate }}</div>
            <div class="i360-search-section" *ngIf="firewallService.counts?.server && firewallService.counts?.cloud">
                <i360-list-count item="white"></i360-list-count>
                <i360-list-count item="drop"></i360-list-count>
                <i360-list-count item="captcha"></i360-list-count>
                <i360-list-count item="splashscreen"></i360-list-count>
            </div>
        </div>
    `,
    styleUrls: ['./list-counts.component.scss'],
})
export class ListCountsComponent {
    @Input() widgetsHasParams: boolean = false;

    constructor(
        public firewallService: FirewallCountsInterceptor
    ) {}
}
