import { Component, TemplateRef, ViewChild } from '@angular/core';
import { BlockedRulesService } from 'app/services/blocked-rules';
import { BlockedRule, RulePlugin } from '@imunify360-api/blocked-rules';
import { useLoader } from 'app/utils/loader/decorators';
import { I360Validators } from 'app/utils/validator/validators';
import { AddItemFormComponent } from 'app/utils/add-item/add-item-form/add-item-form.component';
import { NotificationsService } from 'app/services/notifications';
import { ModalService } from 'app/utils/modals/modal.service';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { TableComponent } from 'app/utils/table/component/table.component';
import { I360FormBuilder } from 'app/utils/validator/form-builder';


@Component({
    selector: 'settings-waf',
    styleUrls: ['./blocked-rules.component.scss'],
    templateUrl: './blocked-rules.component.html',
})
export class SettingsBlockedRulesComponent {
    imunifyPackage = IMUNIFY_PACKAGE;
    form = this.formBuilder.group({
        id: this.formBuilder.control('',
            [I360Validators.required('rule_id')]),
        plugin: this.formBuilder.control({
                value: RulePlugin.ossec,
                allowedValues: [
                    {value: RulePlugin.ossec, title: RulePlugin.ossec},
                    {value: RulePlugin.modsec, title: RulePlugin.modsec},
                    {value: RulePlugin.lfd, title: RulePlugin.lfd},
                    {value: RulePlugin.cphulk, title: RulePlugin.cphulk},
                ]},
            [I360Validators.required('plugin')]),
        domains: this.formBuilder.control('', [I360Validators.commaSeparatedDomains]),
        name: this.formBuilder.control(''),
    });

    @ViewChild(AddItemFormComponent, {static: true}) addItemForm: AddItemFormComponent;
    @ViewChild(RequestDirective, {static: true}) public request: RequestDirective;
    @ViewChild(TableComponent, {static: true}) public table: TableComponent;
    @ViewChild('modal', {static: true}) public modalBody: TemplateRef<{
        id: string;
    }>;

    constructor(
        public blockedRulesService: BlockedRulesService,
        private notifications: NotificationsService,
        private modalService: ModalService,
        private formBuilder: I360FormBuilder,
    ) {}

    @useLoader
    async addRule() {
        const rule = new BlockedRule(this.form.value);
        await this.blockedRulesService.disable(rule);
        this.notifications.success('notifications.ruleAction', null, {action: 'disabled'});
        this.form.reset();
        this.addItemForm.addingItem = false;
        await this.request.load();
    }

    @useLoader
    async enable(rule: BlockedRule) {
        if (false === await this.modalService.open(this.modalBody, {
            id: 'enable-rule',
            data: rule,
        })) {
            return;
        }

        await this.blockedRulesService.enable(rule);
        this.notifications.success('notifications.ruleAction', null, {action: 'enabled'});
        await this.request.load();
    }

    @useLoader
    async saveDomains(newDomains: string[], rule: BlockedRule) {
        const newRule = new BlockedRule({
            id: rule.id,
            plugin: rule.plugin,
            name: rule.name,
            domains: newDomains,
        });
        await this.blockedRulesService.edit(newRule);
        this.notifications.success('notifications.ruleAction', null, {action: 'edited'});
        rule.setDomains(newDomains);
        this.table.detector.markForCheck();
    }
}
