import { Component, OnInit, ViewChild } from '@angular/core';
import { LicenseService } from 'app/services/license';
import { useLoader } from 'app/utils/loader/decorators';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { I360Validators } from 'app/utils/validator/validators';
import { LicenseState } from 'app/services/license-state';
import { firstValueFrom } from 'rxjs';

declare const i360PleskSettings: {
    buyUrl: string,
    marketplace: boolean,
    licenseType: string,
};

const DEFAULT_LINK = 'https://cln.cloudlinux.com';
const DEFAULT_LINK_TO_BUY = 'https://cln.cloudlinux.com/console/purchase/imunify/acquire';


@Component({
    selector: 'license',
    templateUrl: './license.component.html',
    styleUrls: ['./license.component.scss'],
})
export class LicenseComponent implements OnInit {
    form: UntypedFormGroup;
    linkToBuy: string = DEFAULT_LINK_TO_BUY;
    fromMarketPlace = false;
    @ViewChild(LoaderDirective, {static: true}) loader: LoaderDirective;

    public get linkToCln() {
        return this.licenseState.license.value.redirect_url || DEFAULT_LINK;
    }

    constructor(
        public licenseState: LicenseState,
        public licenseService: LicenseService,
        formBuilder: UntypedFormBuilder,
    ) {
        if (typeof i360PleskSettings !== 'undefined' && i360PleskSettings) {
            this.fromMarketPlace = i360PleskSettings.marketplace;
            this.linkToBuy = this.fromMarketPlace ? i360PleskSettings.buyUrl : this.linkToBuy;
        }

        this.form = formBuilder.group({
            licenseKey: formBuilder.control('', [I360Validators.required('key')]),
        });
    }

    public navigateToMarket() {
        window.open(this.linkToBuy, '_blank');
    }

    @useLoader
    public async saveLicenseKey() {
        await this.licenseService.register(this.form.value.licenseKey);
    }

    @useLoader
    async ngOnInit() {
        if (this.fromMarketPlace) {
            await firstValueFrom(this.licenseService.registerViaPanel(), { defaultValue: undefined });
        }
    }
}
