import { Injectable } from '@angular/core';
import { BlockedPort, BlockedPortsAPI } from '@imunify360-api/blocked-ports';
import { XHR } from 'app/utils/xhr/xhr';


@Injectable()
export class BlockedPortsService {
    addPort = this.xhr.request(BlockedPortsAPI.addPort);
    addIp = this.xhr.request(BlockedPortsAPI.addIp);
    deletePort = this.xhr.request(BlockedPortsAPI.deletePort);
    deleteIp = this.xhr.request(BlockedPortsAPI.deleteIp);
    editPortComment = this.xhr.request(BlockedPortsAPI.editPortComment);
    editIpComment = this.xhr.request(BlockedPortsAPI.editIpComment);

    get = this.xhr.wrap(this.xhr.rx(BlockedPortsAPI.list, false), BlockedPort);

    constructor(
        public xhr: XHR,
    ) {}
}
