import { Component, EventEmitter, Input, Output } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';

@Component({
    selector: 'i360-inline-datepicker',
    templateUrl: './inline-datepicker.component.html',
    styleUrls: ['./inline-datepicker.component.scss'],
})
export class InlineDatepickerComponent {
    @Input() value = 0;
    @Output() save = new EventEmitter();
    form: UntypedFormGroup;
    removing = false;
    constructor(
        private formBuilder: UntypedFormBuilder,
    ) {}

    open() {
        this.form = this.formBuilder.group({
            expiration: this.formBuilder.control(this.value),
        });
    }

    remove() {
        this.removing = true;
    }

    done() {
        const value = this.removing ? 0 : this.form.value.expiration;

        if (this.value !== value) {
            this.save.emit(value);
        }

        this.removing = false;
    }
}
