import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { BackupSystem, BackupSystemState, ClPaymentStatus } from '@imunify360-api/backup-systems';
import {
    fromEvent, interval, of, race, EMPTY, Observable, Subject,
    catchError, first, share, switchMap, takeUntil, tap, concatWith
} from 'rxjs';

import { WindowToken } from 'app/core/window';
import { NotificationsService } from 'app/services/notifications';
import { BackupState } from 'app/components/settings/backup/backup-state';
import { BackupSystemsService } from 'app/services/backup-systems';
import { LoaderDirective } from 'app/utils/loader/loader.directive';


export const MESSAGE_FROM_CLN = 'cloudlinux_backup_purchased';

@Component({
    selector: 'i360-cl-backup',
    templateUrl: './cloudlinux-backup.component.html',
})
export class CloudlinuxBackupComponent implements OnDestroy, OnInit {
    loaderMessage: string;
    private destroyed = new Subject<void>();
    private status: ClPaymentStatus;
    private orderUrl: string;
    private checkClRequest = of(null).pipe(
        switchMap(() => this.service.checkCloudlinux()),
        tap(response => {
            this.status = response.data.items.status;
            if (response.data.items.status === ClPaymentStatus.unpaid) {
                this.orderUrl = response.data.items.url;
            }
        }),
        share(),
    );

    constructor(private notifications: NotificationsService,
                public state: BackupState,
                private loader: LoaderDirective,
                private service: BackupSystemsService,
                @Inject(WindowToken) private window: Window) {
        state.state.pipe(
            takeUntil(this.destroyed),
        ).subscribe(async state => {
            switch (state) {
                case BackupSystemState.init:
                    this.loaderMessage = 'settings.backup.systems.notifications.installing';
                    break;
                case BackupSystemState.activation:
                    this.loaderMessage = 'settings.backup.systems.notifications.activation';
                    break;
                default:
                    this.loaderMessage = '';
                    break;
            }
        });
    }

    openOrderWindow() {
        const subject = race(
            fromEvent(window, 'message').pipe(
                first((event: MessageEvent) => event.data === MESSAGE_FROM_CLN),
            ),
            interval(1000).pipe(
                first(() => !clnWindow || clnWindow.closed),
                tap(() => {
                    throw new Error('settings.backup.systems.notifications.windowClosed');
                }),
            ),
        );
        const clnWindow = this.window.open(this.orderUrl);
        return subject;
    }

    init() {
        this.state.state.next(BackupSystemState.activation);
        this.payment().pipe(
            concatWith(this.state.init({backend: BackupSystem.cloudlinux})),
            catchError(async e => {
                this.state.state.next(BackupSystemState.not_running);
                this.notifications.error(e.message);
                return EMPTY;
            }),
            takeUntil(this.destroyed),
        ).subscribe();
    }

    payment(): Observable<null | number | MessageEvent> {
        if (this.status === ClPaymentStatus.paid) {
            return of(null);
        } else {
            return this.openOrderWindow();
        }
    }

    ngOnDestroy() {
        this.destroyed.next();
    }

    ngOnInit() {
        if (!this.state.enabled.getValue()
            && this.state.state.getValue() === BackupSystemState.not_running) {
            this.checkClRequest.pipe(
                catchError(() => EMPTY),
                takeUntil(this.destroyed),
            ).subscribe();
        }
    }
}
