import { Component, ViewChild } from '@angular/core';
import { BackupState } from 'app/components/settings/backup/backup-state';
import { I360Validators } from 'app/utils/validator/validators';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { BackupSystem } from '@imunify360-api/backup-systems';
import { SpinnerLoaderDirective } from 'app/utils/loader/spinner-loader.directive';
import { useLoader } from 'app/utils/loader/decorators';
import { lastValueFrom } from 'rxjs';

@Component({
    selector: 'i360-cluster-logics-backup',
    templateUrl: './cluster-logics-backup.component.html',
    styleUrls: ['./cluster-logics-backup.component.scss',
        '../../credentials/backup-credentials.component.scss'],
})
export class ClusterlogicsBackupComponent {
    form = this.formBuilder.group({
        url: this.formBuilder.control<string>(''),
        username: this.formBuilder.control('', [I360Validators.required('username')]),
        apikey: this.formBuilder.control('', [I360Validators.required('api_key')]),
    });
    @ViewChild(SpinnerLoaderDirective, {static: true}) loader: SpinnerLoaderDirective;

    constructor(public state: BackupState, private formBuilder: I360FormBuilder) {}
    @useLoader
    init() {
        const {url, username, apikey} = this.form.value;
        return lastValueFrom(this.state.init({
            backend: BackupSystem.clusterlogics,
            username,
            apikey,
            ...(url ? {url} : {}),
        }), { defaultValue: {} });
    }
}
