import { Component, ViewChild } from '@angular/core';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { AppState } from 'app/core/app.service';
import { Panel } from 'app/utils/panel';
import { useLoader } from 'app/utils/loader/decorators';
import { I360Config } from 'app/utils/config/config';
import { ModalService } from 'app/utils/modals/modal.service';
import { timer, firstValueFrom, tap } from 'rxjs';
import { NotificationsService } from 'app/services/notifications';
import { PortBlockingMode } from '@imunify360-api/settings';
import {
    BlockedPortsModalComponent,
} from 'app/components/blocked-ports/blocked-ports-modal/blocked-ports-modal.component';


@Component({
    selector: 'i360-blocked-ports-form',
    templateUrl: './blocked-ports-form.component.html',
    styleUrls: [`./blocked-ports-form.component.scss`,
        `../../settings/settings-index.component.scss`],
})
export class BlockedPortsFormComponent {
    PortBlockingMode = PortBlockingMode;
    @ViewChild(LoaderDirective, {static: true}) public loader: LoaderDirective;

    public constructor(
        public appState: AppState,
        public panel: Panel,
        public i360Config: I360Config,
        public modalService: ModalService,
        public notifications: NotificationsService,
    ) {}

    confirmReload() {
        return !this.appState.csf;
    }

    @useLoader
    async submit() {
        if (this.i360Config.schema.value.FIREWALL.port_blocking_mode === PortBlockingMode.allow
            || await this.modalService.open(BlockedPortsModalComponent, {
                data: this.i360Config.schema.value.FIREWALL,
            })) {
            await firstValueFrom(
                this.i360Config.save(false).pipe(
                    tap(() => {
                        this.notifications.success(
                            'settings.successSaveMessage',
                        );
                    }),
                ),
                { defaultValue: undefined }
            );
        }
    }

    @useLoader
    async onChangeBlockingMode(portBlockingMode: PortBlockingMode) {
        if (portBlockingMode === PortBlockingMode.allow) {
            // wait for hide unnecessary fields from the form
            await firstValueFrom(timer(10));
            await this.submit();
        }
    }
}
