import { RequestParams } from './misc';

// enums can not be extended...
export const KERNELCARE_STATUS_FETCHING = 'Fetching';

export const unsupportedErrors  = [
    'Not supported',
    'Kernel is unsupported!',
    'Kernel is unsupported: Inside container!',
];

export interface KernelCareData {
    autoUpdate: boolean;
    license: number;
    uptime: number;
    realKernel: string;
    loadedPatchLevel: number;
    effectiveKernel: string;
    updateCode: number;
}

export class KernelCareItem {
    autoUpdate: boolean;
    license: number;
    uptime: string;
    realKernel: string;
    loadedPatchLevel: number;
    effectiveKernel: string;
    updateCode: number;

    constructor(data: KernelCareData) {
        Object.assign(this, data);
    }
}

export interface KernelCareResponse {
    items: KernelCareItem;
}

export class KernelcareAPI {
    static kcarectl = () => new RequestParams<KernelCareResponse>(['kcarectl', 'plugin-info'], {});
    static enableAutoUpdate = () => new RequestParams(['kcarectl', 'enable-auto-update']);
    static disableAutoUpdate = () => new RequestParams(['kcarectl', 'disable-auto-update']);
    static update = () => new RequestParams(['kcarectl', 'update']);
    static patchInfo = () => new RequestParams(['kcarectl', 'patch-info']);
}
