import { DataFrom, RequestParams } from './misc';
import { ConfigAgentResponse } from './settings';

export type AdvicesListsType = Array<{
    type: string,
    snoozed_until: number,
    dashboard: boolean,
    popup: boolean,
    popup_title: string,
    popup_description: string,
    config_action: {[key: string]: {[key: string]: any}},
    ignore: {[key: string]: {[key: string]: any[]}},
    description: string;
    severity: number,
    link_text: string,
    link: string
}>;

export type SnoozeRequestType = {
    type: string[],
    until: string,
};

export class AdvisorApi {
    static applyAdvisor(advices: AdvicesListsType) {
        return new RequestParams<DataFrom<ConfigAgentResponse>>(
            ['advisor', 'apply'],
            {
                advices,
            },
        );
    }
}

