import { I360Validators } from '../validator/validators';
import { Mode } from '@imunify360-api/proactive-defense';
import { getSchema as getAntivirusConfig } from './antivirus-config';
import { getAdminEnduserMalwareCommonConfig } from './malware-config';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { Validators } from '@angular/forms';
import { ConfigTypeExtractingHelper, PermissionsType } from 'app/utils/config/misc';
import { PortBlockingMode, RuleSet } from '@imunify360-api/settings';

export const getSchema = (formBuilder: I360FormBuilder) => ({
    ...getAntivirusConfig(formBuilder),
    DOS: formBuilder.group({
        enabled: formBuilder.control(true),
        default_limit: formBuilder.control(1,
            [
                I360Validators.integer(true),
                Validators.min(100),
            ]),
        interval: formBuilder.control(1,
            [
                I360Validators.integer(true),
                Validators.min(30),
            ]),
    }),
    AUTO_WHITELIST: formBuilder.group({
        timeout: formBuilder.control(0,
            [I360Validators.integer(true), Validators.min(0), Validators.max(70000)]),
    }),
    INCIDENT_LOGGING: formBuilder.group({
        min_log_level: formBuilder.control(1,
            [I360Validators.integer(true), Validators.min(1), Validators.max(15)]),
        num_days: formBuilder.control(1,
            [I360Validators.integer(true), Validators.min(1)]),
        limit: formBuilder.control(1,
            [I360Validators.integer(true), Validators.min(1)]),
        ui_autorefresh_timeout: formBuilder.control(10,
            [I360Validators.integer(true), Validators.min(5)]),
    }),
    SEND_ADDITIONAL_DATA: formBuilder.group({
        enable: formBuilder.control(false),
    }),
    MALWARE_SCANNING: formBuilder.group({
        rapid_scan: formBuilder.control(false),
        detect_elf: formBuilder.control(false),
        enable_scan_inotify: formBuilder.control(false),
        optimize_realtime_scan: formBuilder.control(true),
        enable_scan_modsec: formBuilder.control(false),
        enable_scan_pure_ftpd: formBuilder.control(false),
        sends_file_for_analysis: formBuilder.control(true),
        ...getAdminEnduserMalwareCommonConfig(formBuilder),
        notify_on_detect: formBuilder.control(false),
        hyperscan: formBuilder.control(false),
        enable_scan_cpanel: formBuilder.control(true),
    }),
    MALWARE_SCAN_INTENSITY: formBuilder.group({
        cpu: formBuilder.control(7),
        io: formBuilder.control(7),
    }),
    BACKUP_RESTORE: formBuilder.group({
        max_days_in_backup: formBuilder.control(0, [I360Validators.integer(true)]),
    }),
    MALWARE_DATABASE_SCAN: formBuilder.group({
        enable: formBuilder.control(true),
    }),
    PROACTIVE_DEFENCE: formBuilder.group({
        mode: formBuilder.control<Mode>(null),
        blamer: formBuilder.control(false),
        php_immunity: formBuilder.control(true),
    }),
    WEBSHIELD: formBuilder.group({
        enable: formBuilder.control(false),
        known_proxies_support: formBuilder.control(false),
        splash_screen: formBuilder.control(false),
        panel_protection: formBuilder.control(false),
    }),
    OSSEC: formBuilder.group({
        active_response: formBuilder.control(false),
    }),
    ADMIN_CONTACTS: formBuilder.group({
        emails: formBuilder.control<string[]>([], [
            I360Validators.emails,
        ]),
    }),
    CSF_INTEGRATION: formBuilder.group({
        catch_lfd_events: formBuilder.control(false),
    }),
    PAM: formBuilder.group({
        enable: formBuilder.control(true),
        exim_dovecot_protection: formBuilder.control(true),
        ftp_protection: formBuilder.control(false),
    }),
    SMTP_BLOCKING: formBuilder.group({
        enable: formBuilder.control(false),
        ports: formBuilder.control<string[]>([], [I360Validators.listOfIntegers(1, 65_535)]),
        allow_users: formBuilder.control<string[]>([]),
        allow_groups: formBuilder.control<string[]>([]),
        allow_local: formBuilder.control(false),
        redirect: formBuilder.control(false),
    }),
    KERNELCARE: formBuilder.group({
        edf: formBuilder.control(false),
    }),
    MOD_SEC: formBuilder.group({
        app_specific_ruleset: formBuilder.control(false),
        ruleset: formBuilder.control(RuleSet.FULL),
        cms_account_compromise_prevention: formBuilder.control(false),
    }),
    FIREWALL: formBuilder.group({
        port_blocking_mode: formBuilder.control<PortBlockingMode | string>({
            value: '',
            allowedValues: [{
                value: PortBlockingMode.allow,
                title: 'blockedPorts.defaultBlocking.options.open',
                experimental: false,
            }, {
                value: PortBlockingMode.deny,
                title: 'blockedPorts.defaultBlocking.options.close',
                experimental: true,
            }],
        }),
        TCP_IN_IPv4: formBuilder.control<string[]>([],
            [I360Validators.listPortOrRange()]),
        TCP_OUT_IPv4: formBuilder.control<string[]>([],
            [I360Validators.listPortOrRange()]),
        UDP_IN_IPv4: formBuilder.control<string[]>([],
            [I360Validators.listPortOrRange()]),
        UDP_OUT_IPv4: formBuilder.control<string[]>([],
            [I360Validators.listPortOrRange()]),
    }),
});

const a = () => (null as any as I360FormBuilder).group(getSchema(null as any));
export type AdminConfigType = ConfigTypeExtractingHelper<typeof a> & PermissionsType;
