import dayjs from 'dayjs';
import { Injectable } from '@angular/core';
import {
    of, timer, BehaviorSubject,
    catchError, map, pairwise, repeatWhen, retry, share, switchMap, takeWhile, tap, concatWith,
} from 'rxjs';

import {
    BackupSystem,
    BackupSystemState,
    ExtendedBackupSystemStatusAgentResponse,
    InitArgs,
    Schedule,
    ScheduleType, ScheduleTime,
} from '@imunify360-api/backup-systems';
import { BackupSystemsService } from 'app/services/backup-systems';
import { NotificationsService } from 'app/services/notifications';
import { ModalService } from 'app/utils/modals/modal.service';
import { DisableModalComponent } from './disable-modal/disable-modal.component';
import { TranslateService, UntranslatableString } from 'app/utils/translate/translate.service';


export const STATUS_UPDATE_INTERVAL = 5000;

export async function getScheduleDate(schedule: Schedule,
                                      translate: TranslateService): Promise<string> {
    const asyncFor = async (arr: string[]): Promise<string> => {
        let el: string[] = [];
        for (const i of arr) {
            el.push(await translate.t(`backup.date.${i}`));
        }
        return el.join(', ');
    };

    const weekDays = (wd) => {
        const cloneWd = [...wd];
        switch (wd.sort().toString()) {
            case 'friday,monday,thursday,tuesday,wednesday':
                return translate.t('backup.date.mondayToFriday');
            case 'friday,monday,saturday,sunday,thursday,tuesday,wednesday':
                return translate.t('backup.date.everyDay');
            default:
                return asyncFor(cloneWd);
        }
    };

    const ofEachMonth = await translate.t(`backup.date.ofEachMonth`);

    switch (schedule.type) {
        case ScheduleType.monthlyByDaysOfWeek:
            const occurence = await asyncFor(schedule.occurence);
            const daysOfWeek = await asyncFor(schedule.daysOfWeek);
            return `${occurence} ${daysOfWeek} ${ofEachMonth}`;
        case ScheduleType.monthlyByDaysOfMonth:
            const length = schedule.dayNumbers.length - 1;
            if (schedule.dayNumbers[length] === 'last') {
                schedule.dayNumbers[length] = await translate.t(`backup.date.lastDay`);
            }
            const days = schedule.dayNumbers.join(', ');
            return `${days} ${ofEachMonth}`;
        case ScheduleType.weekly:
        case ScheduleType.hourly:
        case ScheduleType.daily:
            return weekDays(schedule.daysOfWeek);
        case ScheduleType.timeSinceBackup:
            const key = schedule.intervalSinceLastBackup.type === 'days'
                ? `backup.date.timeSinceBackupDays`
                : `backup.date.timeSinceBackupHours`;
            return translate.t(key, {count: schedule.intervalSinceLastBackup.value});
        case ScheduleType.onStartup:
            return translate.t(`backup.date.onStartup`);
        case ScheduleType.onShutdown:
            return translate.t(`backup.date.onShutdown`);
        default:
            throw new Error('Wrong schedule type');
    }
}

export function getScheduleTime(schedule: Schedule, translate: TranslateService): Promise<string> {
    const format = (m: ScheduleTime) => dayjs().hour(m.hour).minute(m.minute).format('HH:mm');

    if ('repeatInterval' in schedule) {
        return translate.t('backup.time.repeat', {
            repeatInterval: schedule.repeatInterval.value,
            intervalType: schedule.repeatInterval.type,
            from: format(schedule.from),
            to: format(schedule.to),
        });
    } else if ('startAt' in schedule) {
        return translate.t('backup.time.at', {startAt: format(schedule.startAt)});
    } else {
        return Promise.resolve('');
    }
}

const ONE_GB = 1024 ** 3;

@Injectable()
export class BackupState {
    states = BackupSystemState;
    backends = BackupSystem;
    state = new BehaviorSubject<BackupSystemState | null>(null);
    backupSystem = new BehaviorSubject<BackupSystem | null>(null);
    title: string;
    enabled = new BehaviorSubject(false);
    logPath: string;
    username: string;
    allowedBackupSystems: BackupSystem[] = [];
    cl: {
        usedSpaceGb: number;
        purchasedBackupGb: number;
        resizeUrl: string;
        region: string;
        schedule: string;
        progress: null | number;
        loginUrl: string;
    } = {} as any;
    clusterlogics: {
        username: string,
        url: string,
        apikey: string,
    };

    statusRequest = this.service.status().pipe(
        tap(res => {
            this.handleStatusResponse(res);
        }),
        repeatWhen(notifications => {
            return notifications.pipe(
                switchMap(() => timer(STATUS_UPDATE_INTERVAL)),
            );
        }),
        takeWhile(res => {
            return !res.data.items.error &&
                [BackupSystemState.init, BackupSystemState.backup].includes(res.data.items.state);
        }),
        retry(),
        share(),
    );

    constructor(private service: BackupSystemsService,
                private modalService: ModalService,
                private translate: TranslateService,
                private notifications: NotificationsService) {
        this.backupSystem.subscribe(async backend => {
            if (backend) {
                this.title = await this.translate.t(`backup.systems.${backend}`);
            }
        });
        this.state.pipe(
            pairwise(),
        ).subscribe( async ([prev, curr]) => {
            const states = [BackupSystemState.backup, BackupSystemState.done];
            if (prev && !states.includes(prev) && curr && states.includes(curr)) {
                this.notifications.success('settings.backup.systems.notifications.configured');
            }

            if (prev && states.includes(prev) && curr && !states.includes(curr)) {
                this.notifications.success('settings.backup.systems.notifications.disabled');
            }
        });
    }

    async disable() {
        const modalResult = await this.modalService.open(DisableModalComponent);
        const backupSystem = this.backupSystem.getValue();

        if (modalResult === false || !backupSystem) {
            return of(false);
        } else {
            return (backupSystem === BackupSystem.cloudlinux
                ? this.service.disableCloudlinux(modalResult.destroyBackups)
                : this.service.disable(backupSystem)).pipe(
                    concatWith(this.statusRequest),
                    map(() => true),
                );
        }
    }

    init(args: InitArgs) {
        return this.service.init(args).pipe(
            catchError(() => of(null)),
            concatWith(this.statusRequest),
        );
    }

    loadBackupSystems() {
        return this.service.list().pipe(
            tap(response => {
                this.allowedBackupSystems = response.data.items
                    .filter(value => value in BackupSystem && value !== BackupSystem.r1soft);
            }),
        );
    }

    private async handleStatusResponse(res: ExtendedBackupSystemStatusAgentResponse) {
        const data = res.data.items;
        this.backupSystem.next(data.backup_system);
        this.enabled.next(data.enabled);
        this.cl = {} as any;
        this.clusterlogics = {} as any;
        if (data.error) {
            this.notifications.error(
                UntranslatableString.for(data.error),
                'settings.backup.systems.notifications.errorDuringStage',
                {state: data.state},
            );
            if (data.enabled) {
                this.state.next(BackupSystemState.done);
            } else {
                this.state.next(BackupSystemState.not_running);
            }
        } else {
            this.state.next(data.state);
        }
        if ((data.backup_system === BackupSystem.cloudlinux ||
            data.backup_system === BackupSystem.cloudlinux_on_premise ||
            data.backup_system === BackupSystem.acronis)
            && data.state === BackupSystemState.init) {
            this.logPath = data.log_path;
        }
        if ((data.backup_system === BackupSystem.cloudlinux ||
            data.backup_system === BackupSystem.cloudlinux_on_premise)
            && (data.state === BackupSystemState.backup ||
                data.state === BackupSystemState.done ||
                (data.state === BackupSystemState.not_running && data.enabled))) {
            if (data.state === BackupSystemState.backup) {
                this.cl.progress = data.progress;
            }
            if (data.backup_system === BackupSystem.cloudlinux) {
                this.cl.purchasedBackupGb = data.purchased_backup_gb;
                this.cl.usedSpaceGb = data.backup_space_used_bytes / ONE_GB;
                this.cl.region = data.region;
                this.cl.resizeUrl = data.resize_url;
            }
            this.cl.loginUrl = data.login_url;
            if (data.schedule) {
                this.cl.schedule =
                    await getScheduleDate(data.schedule, this.translate) +
                    await getScheduleTime(data.schedule, this.translate);
            }
        }
        if ((data.backup_system === BackupSystem.acronis
            && data.state === BackupSystemState.done) ||
            (data.backup_system === BackupSystem.cloudlinux_on_premise
                && (data.state === BackupSystemState.done ||
                    data.state === BackupSystemState.backup))) {
            this.username = data.username;
        }
        if (data.backup_system === BackupSystem.clusterlogics
            && data.state === BackupSystemState.done) {
            this.clusterlogics.username = data.username;
            this.clusterlogics.url = data.url;
            this.clusterlogics.apikey = data.apikey;
        }
    }
}
