import { Component } from '@angular/core';
import { I360Validators } from 'app/utils/validator/validators';
import { ModalEntry } from 'app/utils/modals/entry';
import { I360FormBuilder } from 'app/utils/validator/form-builder';

const getForm = (formBuilder: I360FormBuilder) => formBuilder.group({
    ip: formBuilder.control('', [I360Validators.ipWithCIDR(true)]),
    comment: formBuilder.control(''),
});
// https://github.com/Microsoft/TypeScript/issues/4233#issuecomment-139978012
const e = (<E>(f: () => E) => null as any as E)(() => getForm(null as any));

@Component({
    templateUrl: './add-ip-to-port-modal.component.html',
    styleUrls: ['./add-ip-to-port-modal.component.scss'],
})
export class AddIpToPortModalComponent extends ModalEntry<undefined, typeof e.value> {
    form = getForm(this.formBuilder);
    constructor(private formBuilder: I360FormBuilder) {
        super();
    }
}
