import { Component, ViewChild } from '@angular/core';
import { useLoader } from 'app/utils/loader/decorators';
import { SettingsBackupComponent } from 'app/components/settings/backup/backup.component';
import { BackupState } from 'app/components/settings/backup/backup-state';
import { SpinnerLoaderDirective } from 'app/utils/loader/spinner-loader.directive';
import { LocalBackupSystem } from '@imunify360-api/backup-systems';
import { takeUntil } from 'rxjs';

@Component({
    selector: 'i360-local-backup',
    templateUrl: './local-backup.component.html',
})
export class LocalBackupComponent {
    @ViewChild(SpinnerLoaderDirective, {static: true}) loader: SpinnerLoaderDirective;
    constructor(
        public backupComponent: SettingsBackupComponent,
        public state: BackupState,
    ) {}

    @useLoader
    async init() {
        await this.state
            .init({backend: this.state.backupSystem.getValue() as LocalBackupSystem}).pipe(
                takeUntil(this.backupComponent.destroyed),
            ).toPromise();
    }
}
