import { Injectable } from '@angular/core';
import { FeaturesAPI, FeatureStatus, FeatureStatusDict } from '@imunify360-api/features';
import {
    concat, of, timer, Observable, ReplaySubject,
    catchError, map, multicast, repeatWhen, share, switchMap, take, takeWhile,
} from 'rxjs';
import { XHR } from 'app/utils/xhr/xhr';

export const PERIOD_OF_STATUS_UPDATE = 3000;

@Injectable()
export class FeaturesService {
    constructor(public xhr: XHR) {}

    status = this.xhr.rx(FeaturesAPI.status, true, false);
    action = this.xhr.rx(FeaturesAPI.action);

    getStatus(getName: () => string): Observable<FeatureStatusDict> {
        // @ts-ignore
        return concat(
            of({status: FeatureStatus.detecting as FeatureStatus.detecting}),
            of(null).pipe(
                switchMap(() => this.status(getName(), false, false)),
                map(res => res.data.items),
                catchError(this.handleError),
                repeatWhen(notifications => {
                    return notifications.pipe(
                        switchMap(() => timer(PERIOD_OF_STATUS_UPDATE)),
                    );
                }),
                // eslint-disable-next-line max-len
                // https://stackoverflow.com/questions/44641246/rxjs-takewhile-but-include-the-last-value
                multicast(
                    () => new ReplaySubject(1),
                    subject => concat(
                        subject.pipe(
                            takeWhile((status: FeatureStatusDict) =>
                                this.isStatusActive(status.status)),
                        ),
                        subject.pipe(take(1)),
                    ),
                ),
                share(),
            ),
        );
    }

    isStatusActive(status: FeatureStatus) {
        return status === FeatureStatus.removing || status === FeatureStatus.installing;
    }

    handleError(e) {
        return of({
            status: FeatureStatus.error as FeatureStatus.error,
            message: e ? (e.messages || e.message) : String(e),
        });
    }
}
