import { forwardRef, Component, Inject } from '@angular/core';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { BackupState } from 'app/components/settings/backup/backup-state';
import { ModalEntry } from 'app/utils/modals/entry';
import { LicenseState } from 'app/services/license-state';

@Component({
    templateUrl: './disable-modal.component.html',
    styleUrls: ['./disable-modal.component.scss'],
})
export class DisableModalComponent extends ModalEntry<undefined, {destroyBackups: boolean}> {
    form = this.formBuilder.group({
        destroyBackups: this.formBuilder.control(false),
    });
    constructor(private formBuilder: I360FormBuilder,
                public licenseState: LicenseState,
                @Inject(forwardRef(() => BackupState)) public backupState: BackupState) {
        super();
    }
}
