import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { forkJoin, of, Subject, firstValueFrom, concatWith, take, takeUntil } from 'rxjs';

import { BackupSystem, BackupSystemState } from '@imunify360-api/backup-systems';
import { BackupState } from 'app/components/settings/backup/backup-state';
import { NotificationsService } from 'app/services/notifications';
import { useLoader } from 'app/utils/loader/decorators';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { I360Validators } from 'app/utils/validator/validators';
import { LicenseState } from 'app/services/license-state';


@Component({
    selector: 'settings-backup',
    styleUrls: ['./backup.component.scss'],
    templateUrl: './backup.component.html',
})
export class SettingsBackupComponent implements OnInit, OnDestroy {
    @ViewChild(LoaderDirective, {static: true}) public loader: LoaderDirective;

    public form = this.formBuilder.group({
        enabled: this.formBuilder.control(false),
        backupSystem: this.formBuilder.control<BackupSystem>(
            null,
            I360Validators.required('backup_system'),
        ),
    });
    public destroyed = new Subject<void>();

    constructor(
        public notifications: NotificationsService,
        private formBuilder: I360FormBuilder,
        public state: BackupState,
        public licenseState: LicenseState,
    ) {
        this.state.state.pipe(
            takeUntil(this.destroyed),
        ).subscribe((state: BackupSystemState) => {
            if ([BackupSystemState.init, BackupSystemState.backup,
                BackupSystemState.activation].includes(state)) {
                this.form.controls.backupSystem.disable();
            } else {
                this.form.controls.backupSystem.enable();
            }
        });
        this.state.backupSystem.pipe(
            takeUntil(this.destroyed),
        ).subscribe(backend => {
            this.form.controls.backupSystem.patchValue(backend);
        });
        this.state.enabled.pipe(
            takeUntil(this.destroyed),
        ).subscribe(enabled => {
            this.form.controls.enabled.patchValue(enabled);
        });
        this.form.controls.backupSystem.valueChanges.pipe(
            takeUntil(this.destroyed),
        ).subscribe(value => {
            if (this.state.backupSystem.getValue() !== value) {
                this.state.backupSystem.next(value);
            }
        });
    }
    @useLoader
    async disable() {
        const successfullyDisabled = await (await this.state.disable()).pipe(
            takeUntil(this.destroyed),
        ).toPromise();
        if (successfullyDisabled) {
            this.form.reset();
        } else {
            this.form.patchValue({enabled: true});
        }
    }

    @useLoader
    async ngOnInit() {
        this.state.statusRequest.pipe(
            takeUntil(this.destroyed),
        ).subscribe();
        await firstValueFrom(forkJoin([
            this.state.loadBackupSystems(),
            this.state.statusRequest.pipe(
                concatWith(of(null)),
                take(1),
            ),
        ]), { defaultValue: undefined });
    }

    ngOnDestroy() {
        this.destroyed.next();
    }
}
