import { Component, TemplateRef, ViewChild } from '@angular/core';

import { IncidentsService } from 'app/services/incidents';
import { BlockedRule } from '@imunify360-api/blocked-rules';
import { UntypedFormBuilder } from '@angular/forms';
import { BlockedRulesService } from 'app/services/blocked-rules';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { NotificationsService } from 'app/services/notifications';
import { useLoader } from 'app/utils/loader/decorators';
import { ListsService, PURPOSE } from 'app/services/lists';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { I360Validators } from 'app/utils/validator/validators';
import { getImagePath } from 'app/core/misc';
import { ModalService } from 'app/utils/modals/modal.service';
import { Incident } from '@imunify360-api/incidents';
import { IPList, IPScope } from '@imunify360-api/lists';
import { TableComponent } from 'app/utils/table/component/table.component';
import { GroupService } from 'app/components/lists/group.service';

/**
 * Temporal incidents table wrapper
 */
@Component({
    templateUrl: './incidents.component.html',
    styleUrls: ['./incidents.component.scss'],
})
export class IncidentsComponent {
    IPList = IPList;
    purpose = PURPOSE;
    IPScope = IPScope;
    addDomainsForm = this.formBuilder.group({
        domains: this.formBuilder.control([], [I360Validators.commaSeparatedDomains]),
    });

    @ViewChild('modal', {static: true}) modal: TemplateRef<BlockedRule>;
    @ViewChild(LoaderDirective, {static: true}) loader: LoaderDirective;
    @ViewChild(RequestDirective, {static: true}) request: RequestDirective;
    @ViewChild(TableComponent, {static: true}) table: TableComponent;
    public imagePath: string = getImagePath();

    constructor(
        public incidentsService: IncidentsService,
        private listsService: ListsService,
        private blockedRulesService: BlockedRulesService,
        public groupService: GroupService,
        private formBuilder: UntypedFormBuilder,
        public notifications: NotificationsService,
        private modalService: ModalService,
    ) {}

    @useLoader
    async setList(
        incidents: Array<Incident & { abuser: string }>,
        list: IPList | null,
        scope?: IPScope,
    ) {
        const ipLists = incidents.reduce(function(filtered:Array<{ ip: string, list: IPList | null }>, item) {
          if (item.abuser) {
             let incident = { ip: item.abuser, list: item.purpose || item.listname };
             filtered.push(incident);
          }
          return filtered;
        }, []);
        if (await this.listsService
            .changeListWithConfirmFromIncidents(ipLists, list, scope)) {
            await this.request.load();
        }
    }

    @useLoader
    async blockRule([incident]: Incident[]) {
        this.addDomainsForm.reset();
        const ruleToBlock = incident.getRule();
        if (false === await this.modalService.open(this.modal, {
            data: ruleToBlock,
        })) {
            return;
        }

        ruleToBlock.domains = this.addDomainsForm.value.domains;

        await this.blockedRulesService.disable(ruleToBlock);
        this.notifications.success('notifications.ruleAction', null, {action: 'disabled'});
        await this.request.load();
    }
}
