import { forwardRef, Component, Inject } from '@angular/core';
import { ModalService } from 'app/utils/modals/modal.service';
import { BehaviorSubject } from 'rxjs';
import { ModalEntry } from 'app/utils/modals/entry';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DataLoaderImpl } from 'app/utils/table/data-loader';
import { TranslateService } from 'app/utils/translate/translate.service';
import { AdminConfigType } from 'app/utils/config/admin-config';


@Component({
    selector: 'i360-blocked-ports-modal',
    templateUrl: './blocked-ports-modal.component.html',
    styleUrls: [`./blocked-ports-modal.component.scss`],
    providers: [{
        provide: DataLoaderImpl,
        useExisting: forwardRef(() => BlockedPortsModalComponent),
    }],
})
export class BlockedPortsModalComponent extends ModalEntry<AdminConfigType['FIREWALL'], false> {
    loaded = new BehaviorSubject([
            {
                title: 'blockedPorts.confirmModal.tableTitles.tcpInbound',
                ports: this.data.TCP_IN_IPv4.join(', '),
            },
            {
                title: 'blockedPorts.confirmModal.tableTitles.tcpOutbound',
                ports: this.data.TCP_OUT_IPv4.join(', '),
            },
            {
                title: 'blockedPorts.confirmModal.tableTitles.udpInbound',
                ports: this.data.UDP_IN_IPv4.join(', '),
            },
            {
                title: 'blockedPorts.confirmModal.tableTitles.udpOutbound',
                ports: this.data.UDP_OUT_IPv4.join(', '),
            },
        ]);
    public constructor(
        @Inject(MAT_DIALOG_DATA) public data: AdminConfigType['FIREWALL'],
        public modalService: ModalService,
        public translate: TranslateService,
    ) {
        super();
    }
}
