import { Injectable } from '@angular/core';
import { responses } from '@imunify360-api/stubs/lists';
import { of, Observable, catchError, filter, map, switchMap, take } from 'rxjs';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { baseEndpoint as globalBaseEndpoint } from '@imunify360-api/server';
import { GroupServerIdResponseInServer } from '@imunify360-api/lists';
import { NotificationsService } from 'app/services/notifications';
import { cache } from '../../utils/cache';
import { LicenseState } from 'app/services/license-state';
import { testMode } from 'app/services/misc';

export const groupIdEndpoint = `${globalBaseEndpoint}get-group-id`;

@Injectable()
export class GroupService {
    group: Observable<string | null> = testMode(this.constructor.name) ?
        of(responses.groupId).pipe(
        map(response => {
            return response.group_id;
        }),
    ) : this.licenseState.license.pipe(
        filter(license => !!license.id),
        take(1),
        switchMap(() => {
            return this.http.get<GroupServerIdResponseInServer>(
                groupIdEndpoint,
                {
                    params: {
                        server_id: this.id,
                    },
                },
            ).pipe(
                map(response => {
                    return response.group_id;
                }),
                catchError((e: HttpErrorResponse) => {
                    this.notifications.error(e.message);
                    return  of(null);
                }),
            );
        }),
        cache(300),
    );
    constructor(
        private http: HttpClient,
        private notifications: NotificationsService,
        private licenseState: LicenseState,
    ) { }

    get id() {
        return this.licenseState.license.value.id || 'test';
    }
}
