import { Component, Input } from '@angular/core';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { I360Validators } from 'app/utils/validator/validators';
import { BackupState } from 'app/components/settings/backup/backup-state';
import { takeUntil } from 'rxjs';
import {
    BackupSystemState,
    BackupSystemWithCredentials,
} from '@imunify360-api/backup-systems';
import { SettingsBackupComponent } from 'app/components/settings/backup/backup.component';
import { ExtendFormGroupDirective } from 'app/utils/validator/extend-form-group.directive';

@Component({
    selector: 'i360-backup-credentials',
    templateUrl: './backup-credentials.component.html',
    styleUrls: ['./backup-credentials.component.scss'],
})
export class BackupCredentialsComponent {
    @Input() backend: BackupSystemWithCredentials;
    form = this.formBuilder.group({
        username: this.formBuilder.control('', [I360Validators.required('username')]),
        password: this.formBuilder.control('', [I360Validators.required('password')]),
    });

    constructor(
        private formBuilder: I360FormBuilder,
        private backupComponent: SettingsBackupComponent,
        public state: BackupState,
    ) {}

    init(form: ExtendFormGroupDirective) {
        this.state.state.next(BackupSystemState.init);
        const {username, password} = this.form.value;
        this.state.init({
            backend: this.backend,
            username,
            password,
        }).pipe(
            takeUntil(this.backupComponent.destroyed),
        ).subscribe();
        form.saveLastInputValuesToBrowser();
    }
}
