import { NoSentryError } from 'app/errors';

export class Repeater  {
    private timeout: number;
    private reject: Function;

    clear() {
        if (this.timeout) {
            clearTimeout(this.timeout);
            this.reject(new NoSentryError);
        }
    }

    wait(seconds: number) {
        this.clear();
        return new Promise((resolve, reject) => {
            this.reject = reject;
            this.timeout = window.setTimeout(resolve, seconds * 1000);
        });
    }
}
