import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AttributionComponent } from 'app/components/attribution/attribution.component';
import { ComingSoonComponent } from 'app/components/coming-soon/coming-soon.component';
import { FirewallComponent } from 'app/components/lists/firewall.component';
import { KernelcareComponent } from 'app/components/kernelcare/kernelcare.component';
import { LicenseComponent } from 'app/components/license/license.component';
import { MaliciousComponent } from 'app/components/malware/malicious/malicious.component';
import { MalwareIndexComponent } from 'app/components/malware/malware-index.component';
import {
    MalwareOnDemandScanComponent,
} from 'app/components/malware/on-demand/on-demand-scan.component';
import {
    SettingsBlockedRulesComponent,
} from 'app/components/settings/blocked-rules/blocked-rules.component';
import {
    SettingsGeneralComponent,
} from 'app/components/settings/general/settings-general.component';
import { SettingsIndexComponent } from 'app/components/settings/settings-index.component';
import {
    SettingsMalwareComponent,
} from 'app/components/settings/malware/settings-malware.component';
import { SettingsBackupComponent } from 'app/components/settings/backup/backup.component';
import { IncidentsComponent } from 'app/components/incidents/incidents.component';
import {
    FeaturesManagementWrapperComponent,
} from 'app/components/settings/features-management/wrapper/wrapper.component';
import { AppComponent } from 'app/components/application/app.component';
import { MalwareHistoryComponent } from 'app/components/malware/history/history.component';
import { IgnoreListComponent } from 'app/components/malware/ignore-list/ignore-list.component';
import { UserListComponent } from 'app/components/malware/user-list/user-list.component';
import { SUPPORT_ROUTE } from 'app/components/support/route';
import { PROACTIVE_ROUTE } from 'app/components/proactive-defense/route';
import { UnsavedConfigRouteGuard } from 'app/core/unsaved-route-guard';
import {
    BlockedPortsFormComponent,
} from 'app/components/blocked-ports/blocked-ports-form/blocked-ports-form.component';
import { FiltersService } from 'app/services/filters';
import { SETTINGS_NOTIFICATION_ROUTE } from 'app/components/settings/notifications/route';
import { FirewallListsComponent } from 'app/components/lists/firewall-lists.component';
import { EULA_ROUTE } from 'app/components/eula/eula.module';
import { LicenseGuard } from 'app/services/license.guard';
import { FirewallService } from 'app/components/lists/firewall.service';


export const routes: Routes = [
    {
        path: '',
        component: AppComponent,
        children: [
            {path: '', redirectTo: 'dashboard', pathMatch: 'full'},
            {
                path: 'dashboard',
                loadChildren: () => import('app/components/dashboard/dashboard.module')
                    .then(m => m.DashboardModule),
                canActivate: [FiltersService],
                runGuardsAndResolvers: 'paramsOrQueryParamsChange',
            },
            {
                path: 'incidents',
                component: IncidentsComponent,
                canActivate: [FiltersService],
                runGuardsAndResolvers: 'paramsOrQueryParamsChange',
            },
            {
                path: 'firewall', component: FirewallComponent,
                children: [
                    {path: '', redirectTo: 'all', pathMatch: 'full'},
                    {
                        path: 'blocked-ports',
                        component: BlockedPortsFormComponent,
                        canDeactivate: [UnsavedConfigRouteGuard],
                    },
                    {path: 'all', component: FirewallListsComponent},
                ],
                canActivate: [FirewallService, FiltersService],
                runGuardsAndResolvers: 'always',
            },
            {
                path: 'settings', component: SettingsIndexComponent,
                children: [
                    {path: '', redirectTo: 'general', pathMatch: 'full'},
                    {
                        path: 'general',
                        component: SettingsGeneralComponent,
                        canDeactivate: [UnsavedConfigRouteGuard],
                    },
                    {
                        path: 'malware',
                        component: SettingsMalwareComponent,
                        canDeactivate: [UnsavedConfigRouteGuard],
                    },
                    {path: 'blocked-rules', component: SettingsBlockedRulesComponent},
                    {path: 'backup', component: SettingsBackupComponent},
                    {path: 'attributions', component: AttributionComponent},
                    SETTINGS_NOTIFICATION_ROUTE,
                    {path: 'features-management', component: FeaturesManagementWrapperComponent},
                ],
            },
            {
                path: 'reputation-management',
                loadChildren: () => import('app/components/reputation/reputation.module')
                    .then(m => m.ReputationModule),
            },
            {path: 'coming-soon', component: ComingSoonComponent},
            {
                path: 'malware',
                component: MalwareIndexComponent,
                children: [
                    {path: '', redirectTo: 'user-list', pathMatch: 'full'},
                    {path: 'user-list', component: UserListComponent},
                    {path: 'files', component: MaliciousComponent},
                    {path: 'on-demand-scan', component: MalwareOnDemandScanComponent},
                    {path: 'history', component: MalwareHistoryComponent},
                    {path: 'ignore-list', component: IgnoreListComponent},
                ],
                canActivate: [FiltersService],
                runGuardsAndResolvers: 'always',
            },
            PROACTIVE_ROUTE,
            // { path: 'patch-management', component: ComingSoonComponent },
            {path: 'kernelcare', component: KernelcareComponent},
            SUPPORT_ROUTE,
            {
                path: 'license',
                component: LicenseComponent,
                canActivate: [LicenseGuard],
                runGuardsAndResolvers: 'paramsOrQueryParamsChange',
            },
            EULA_ROUTE,
        ],
    },
];

@NgModule({
    imports: [
        RouterModule.forChild(routes),
    ],
    exports: [RouterModule],
    providers: [],
})
export class AdminRoutingModule {
}
