import { XHR } from 'app/utils/xhr/xhr';
import { forkJoin, of, EMPTY, Observable, firstValueFrom, catchError, first, switchMap, take, tap } from 'rxjs';
import dayjs from 'dayjs';
import { AdvicesListsResponseInServer, ServerResponse } from '@imunify360-api/dashboard';
import { responses } from '@imunify360-api/stubs/dashboard';
import { Injectable } from '@angular/core';
import { AdvicesListsType, AdvisorApi, SnoozeRequestType } from '@imunify360-api/advisor';
import { HttpClient } from '@angular/common/http';
import { baseEndpoint as globalBaseEndpoint, TEST_SERVER_ID } from '@imunify360-api/server';
import {
    AdvisorModalComponent,
} from 'app/components/dashboard/advisor-modal/advisor-modal.component';
import { ModalService } from 'app/utils/modals/modal.service';
import { LicenseState } from './license-state';
import { testMode } from 'app/services/misc';
import { ConfigRef } from 'app/core/config-ref';

const baseEndpoint = `${globalBaseEndpoint}dashboard/`;
export const advicesListsServerEndpoint = `${baseEndpoint}events`;
export const snoozeAdvicesServerEndpoint = `${baseEndpoint}events/snooze`;


@Injectable()
export class AdvisorService {
    applyAdvisor = this.xhr.rx(AdvisorApi.applyAdvisor);

    constructor(
        private http: HttpClient,
        public licenseState: LicenseState,
        private modalService: ModalService,
        public xhr: XHR,
        private config: ConfigRef,
    ) {
        this.checkAdvices();
    }

    getAdvices(): Observable<AdvicesListsResponseInServer> {
        return this.licenseState.license.pipe(
            first(license => !!license.id),
            switchMap(() => {
                return (TEST && testMode(this.constructor.name))
                    ? of(responses.advices)
                    : this.http.get<AdvicesListsResponseInServer>(advicesListsServerEndpoint, {
                        params: {
                            dashboard: 'false',
                            popup: 'true',
                            not_snoozed_at: dayjs().unix().toFixed(),
                            server_id: this.id,
                            target_server_id: this.id,
                        },
                    });
            }),
        );
    }

    setAdvicesSnooze(requestData: SnoozeRequestType): Observable<ServerResponse<boolean>> {
        return (TEST && testMode(this.constructor.name))
            ? of({result: true})
            : this.http.post<ServerResponse<boolean>>(
                snoozeAdvicesServerEndpoint, null, {
                    params: {
                        server_id: this.id,
                        until: requestData.until,
                        types: requestData.type.join(','),
                    },
                },
            );
    }

    private checkAdvices() {
        this.config.configChange.pipe(
            take(1),
            switchMap(config => {
                if (!config.PERMISSIONS.advisor) {
                    return EMPTY;
                }
                return forkJoin([this.getAdvices(), of(config)]);
            }),
            catchError(() => EMPTY),
            tap(async ([response, config]) => {
                const advices = response.result.filter(advise => {
                    for (const [sectionKey, sectionValue] of Object.entries(advise.ignore)) {
                        for (const [valueKey, ignoredValues] of Object.entries(sectionValue)) {
                            if (Array.isArray(ignoredValues) &&
                                ignoredValues.includes(config[sectionKey][valueKey])) {
                                return false;
                            }
                        }
                    }
                    return true;
                });

                if (advices.length) {
                    let result = await this.modalService.open(AdvisorModalComponent, {
                        data: advices,
                    });
                    if (result) {
                        let advicesApplyData: AdvicesListsType = [];
                        let advicesCanceledData: string[] = [];
                        for (let i = 0; i < result.length; i++) {
                            if (result[i]) {
                                advicesApplyData.push(advices[i]);
                            } else {
                                advicesCanceledData.push(advices[i].type);
                            }
                        }
                        if (advicesCanceledData.length) {
                            await firstValueFrom(this.setAdvicesSnooze({
                                type: advicesCanceledData,
                                until: dayjs().add(3, 'month').unix().toFixed(),
                            }), { defaultValue: undefined });
                        }
                        if (advicesApplyData.length) {
                            const conf = await firstValueFrom(
                                this.applyAdvisor(advicesApplyData), { defaultValue: undefined }
                            );

                            // FIXME: cover with tests
                            this.config.config.requestsState.config.push.next(conf?.data?.items as any);
                        }
                    }
                }
            }),
        ).subscribe();
    }

    get id() {
        return TEST_SERVER_ID || this.licenseState.license.value.id;
    }
}
