import { Injectable } from '@angular/core';
import { Incident, IncidentsAPI } from '@imunify360-api/incidents';
import { XHR } from 'app/utils/xhr/xhr';


@Injectable()
export class IncidentsService {
    constructor(public xhr: XHR) {}

    _get = this.xhr.rx(IncidentsAPI.incidents, false);
    get = this.xhr.wrap(this._get, Incident);
}
