import {
    AfterContentInit, Component, ContentChildren,
    EventEmitter, Output, QueryList,
} from '@angular/core';
import { AddItemTabDirective } from 'app/utils/add-item/add-item-tab/add-item-tab.directive';

@Component ({
    selector: '[i360-add-item-form]',
    templateUrl: './add-item-form.component.html',
    styleUrls: ['./add-item-form.component.scss'],
})

export class AddItemFormComponent implements AfterContentInit {
    addingItem = false;
    @Output() tabChange = new EventEmitter;
    @ContentChildren(AddItemTabDirective) tabs: QueryList<AddItemTabDirective>;
    activeTab: AddItemTabDirective;
    setTab(tab: AddItemTabDirective) {
        if (tab !== this.activeTab) {
            this.activeTab = tab;
            this.tabChange.emit(tab);
        }
    }
    ngAfterContentInit() {
        this.activeTab = this.tabs.first;
    }
}
