import { Subscription, catchError, tap } from 'rxjs';
import { TableComponent } from 'app/utils/table/component/table.component';
import { IPList, IPScope, ListItem } from '@imunify360-api/lists';
import { CountryService } from 'app/services/country';
import { Component, OnDestroy, ViewChild } from '@angular/core';
import { ListsService, PURPOSE } from 'app/services/lists';
import { useLoader } from 'app/utils/loader/decorators';
import { AppState } from 'app/core/app.service';
import { ActivatedRoute } from '@angular/router';
import { NotificationsService } from 'app/services/notifications';
import { I360Validators } from 'app/utils/validator/validators';
import { Panel } from 'app/utils/panel';
import { AddItemFormComponent } from 'app/utils/add-item/add-item-form/add-item-form.component';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { ModalService } from 'app/utils/modals/modal.service';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { I360Config } from 'app/utils/config/config';
import { GroupService } from './group.service';
import {
    FirewallCounts, FirewallCountsInterceptor
} from 'app/interceptors/firewall-counts.interceptor';
import { filterObject } from 'app/utils/helper';
import { XhrNotificator } from 'app/utils/xhr/notificator';
import { getImagePath } from 'app/core/misc';


@Component({
    selector: 'lists',
    styleUrls: ['./firewall-lists.component.scss'],
    templateUrl: './firewall-lists.component.html',
})
export class FirewallListsComponent implements OnDestroy {
    @ViewChild(TableComponent, {static: false}) table: TableComponent;
    @ViewChild(RequestDirective, {static: false}) request: RequestDirective;
    @ViewChild(AddItemFormComponent, {static: false}) addItemForm: AddItemFormComponent;

    purpose = PURPOSE;
    getData: Function;
    I360IpValidator = I360Validators.ipWithCIDR(true);
    IPList = IPList;
    IPScope = IPScope;
    public imagePath: string = getImagePath();
    public form = this.formBuilder.group({
        ip: this.formBuilder.control('', [I360Validators.ipWithCIDR(true)]),
        country: this.formBuilder.control('', [], [this.countryService.validator(true)]),
        comment: this.formBuilder.control(''),
        expiration: this.formBuilder.control<number>(null),
        list: this.formBuilder.control(IPList.white),
        fullAccess: this.formBuilder.control(false),
        scope: this.formBuilder.control(IPScope.local),
    });
    public filterLists: {
      country? : string;
      by_ip?: string;
      comments?: string;
    };
    newItemListMemory;
    noResultFoundInLocal;
    private routeParamsSubscription = Subscription.EMPTY;

    constructor(
        private modalService: ModalService,
        public appState: AppState,
        private listService: ListsService,
        private notifications: NotificationsService,
        public countryService: CountryService,
        private formBuilder: I360FormBuilder,
        public groupService: GroupService,
        public panel: Panel,
        public route: ActivatedRoute,
        public config: I360Config,
        public firewallService: FirewallCountsInterceptor,
        public xhrNotificator: XhrNotificator,
    ) {
        this.getData = this.load.bind(this);
        this.routeParamsSubscription = this.route.queryParams.subscribe(async params => {
            this.filterLists = params;
        });
    }

    load(params) {
        return this.listService.getALL({
            params,
        }).pipe(
            tap(_ => this.noResultFoundInLocal = FirewallListsComponent.checkNoResult(
                params, this.firewallService.counts)
            ),
            catchError(async (err) => {
                await this.request.load(true);
                throw err;
            }),
        );
    }

    static checkNoResult(params, cloudCounts: Partial<FirewallCounts>) {
        const filterIfHasPurpose = ([k, v]) => params.purpose ? params.purpose.includes(k) : true;
        const addValues = (acc: number, cur: number) => acc + cur;
        const data = filterObject(cloudCounts.cloud, filterIfHasPurpose);
        return Boolean(Object.values(data).reduce(addValues));
    }

    @useLoader
    async deleteSelected(items: ListItem[]) {

        if (false === await this.modalService.confirm(`delete-ip`,
            items.map(x => x.isCountry
                ? this.countryService.getCountryNameByCode((x.country).code!) : x.ip))) {
            return;
        }

        let { sortedDataIps, sortedDataCountries } = this.sortItems(items);
        for(let [list, sortedIps] of sortedDataIps) {
            let ips = Array.from(sortedIps);
            let countries = sortedDataCountries.get(list);
            if (ips.length) {
                await this.listService.deleteItems({itemType: 'ip', items: ips, list});
                this.notifications.success(
                    'notifications.ipAction', null, {count: ips.length, action: 'deleted'},
                );
            }
            if (countries && countries.size) {
                await this.listService.deleteItems({itemType: 'country', items: Array.from(countries), list});
                this.notifications.success(
                    'notifications.countryAction', null, {count: countries.size, action: 'deleted'},
                );
            }
        }
        await this.request.load();
    }

    @useLoader
    async changeList(newList: IPList, items: ListItem[]) {
        const {ips} = this.sortItems(items);
        if (await this.listService.changeListWithConfirm(ips, this.IPList.white, newList)) {
            await this.request.load();
        }
    }

    @useLoader
    async inlineAddItem(ip: string, list) {
        this.form.patchValue({ip, list});
        await this.addItem();
    }

    @useLoader
    async addItem() {
        let {ip, country, comment, list, expiration, fullAccess, scope} = this.form.value;
        fullAccess = fullAccess && list === IPList.white;
        const type = ip ? 'ip' : 'country';
        try {
            await this.listService.addOrUpdateItems({
                list,
                type,
                comment,
                scope,
                expiration,
                fullAccess,
                value: ip || this.countryService.searchCountryCodeByName(country),
            });
            this.notifications.success(
                `notifications.${type}Action`, null, {count: 1, action: 'added'},
            );
            this.form.reset({list});
            this.addItemForm.addingItem = false;
            await this.request.load();
        } catch (e) {
            this.xhrNotificator.showResponseErrors(e);
        }
    }

    @useLoader
    async changeScope(items: ListItem[] , scope: IPScope) {
        const ips = items.map(item => item.ip);
        if (await this.modalService.confirm(`scope-to-${scope}`, ips)) {
            let { sortedDataIps } = this.sortItems(items);
            for(let [list, sortedIps] of sortedDataIps) {
                await this.listService.addOrUpdateItems({
                    value:  Array.from(sortedIps),
                    scope,
                    list,
                });
            }
            this.notifications.success(
                `lists.lists.notifications.scope`,
                null,
                {ips, type: scope},
            );
            this.request.load();
        }
    }

    @useLoader
    async grantFullAccess(items: ListItem[]) {
        const ips = items.map(item => item.ip);
        if (await this.modalService.confirm('grant-full-access', ips)) {
            await this.listService.addOrUpdateItems({
                value: ips,
                list: IPList.white,
                fullAccess: true
            });
            this.notifications.success(
                `lists.lists.notifications.fullAccess`,
                null,
                {count: items.length, ips, action: 'granted'},
            );
            this.request.load();
        }
    }

    @useLoader
    async removeFullAccess(items: ListItem[]) {
        const ips = items.map(item => item.ip);
        if (await this.modalService.confirm('remove-full-access', ips)) {
            await this.listService.addOrUpdateItems({
                value: ips,
                list: IPList.white,
                fullAccess: false
            });
            this.notifications.success(
                `lists.lists.notifications.fullAccess`,
                null,
                {count: items.length, ips, action: 'removed'},
            );
            this.request.load();
        }
    }

    public sortItems(list: ListItem[]): {
        ips: string[],
        countries: string[],
        sortedDataIps: Map<IPList | null, Set<string>>,
        sortedDataCountries: Map<IPList | null, Set<string>>,
    } {
        let ips: string[] = [];
        let countries: string[] = [];
        let sortedDataIps = new Map<IPList | null, Set<string>>();
        let sortedDataCountries = new Map<IPList | null, Set<string>>();
        for (let item of list) {
            let ipBylists = sortedDataIps.get(item.list) || new Set();
            let countryBylists = sortedDataCountries.get(item.list) || new Set();
            if (item.isCountry && item.country.code) {
                countries.push(item.country.code);
                countryBylists.add(item.country.code);
            } else {
                ipBylists.add(item.ip);
                ips.push(item.ip);
            }
            sortedDataCountries.set(item.list, countryBylists);
            sortedDataIps.set(item.list, ipBylists);
        }
        return {
            ips: ips,
            countries: countries,
            sortedDataCountries,
            sortedDataIps,
        };
    }

    @useLoader
    public async saveTtl(expiration: number, item: ListItem) {
        expiration = expiration || 0;
        await this.listService.addOrUpdateItems(
            {
                value: [item.ip],
                list: item.list,
                expiration: expiration,
            }
        );
        item.expiration = expiration;
        this.notifications.success(
            `notifications.${item.type}Action`, null, {count: 1, action: 'updated'},
        );
        this.table.detector.markForCheck();
    }

    @useLoader
    public async saveComment(newComment: string, item: ListItem) {
        await this.listService.editItem( {
                items: [item.isCountry ? item.country.code : item.ip],
                list: item.list,
                type: item.type,
                comment: newComment,
            });
        item.comment = newComment;
        this.notifications.success(
            `notifications.${item.type}Action`, null, {count: 1, action: 'updated'},
        );
        this.table.detector.markForCheck();
    }

    onTabChange(tab) {
        const form = this.form;
        if (tab === this.addItemForm.tabs.last) {
            this.newItemListMemory = form.value.list;
            form.patchValue({list: IPList.drop});
        } else if (this.newItemListMemory) {
            form.patchValue({list: this.newItemListMemory});
        }
    }

    ngOnDestroy() {
        this.routeParamsSubscription.unsubscribe();
    }
}
