import { Component, OnDestroy } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { I360Config } from 'app/utils/config/config';
import { PortBlockingMode } from '@imunify360-api/settings';


@Component({
    selector: 'i360-firewall',
    template: `
        <i360-header>{{ 'lists.firewall.title' | translate }}</i360-header>
        <div i360-common-widgets-container #commonWidgets="i360CommonWidgetsContainer">
            <i360-tabs>
                <ng-container>
                    <a *ngFor="let item of menu"
                       [routerLink]="item.route"
                       [queryParams]="{offset: undefined}"
                       [class.active]="item.route === lastSegment">
                        <span>{{ item.title | translate }}</span>
                    </a>
                </ng-container>
            </i360-tabs>
            <router-outlet></router-outlet>
        </div>
    `,
    styleUrls: ['./firewall.component.scss'],
})
export class FirewallComponent implements OnDestroy {
    lastSegment: string;
    PortBlockingMode = PortBlockingMode;
    menu: MenuItem[] = [
        new MenuItem('all', 'lists.firewall.menu.all'),
        new MenuItem('blocked-ports', 'lists.firewall.menu.blockedPorts', true),
    ];

    // dirty hack to avoid disappearing link's active class when using table sorting
    private routerEventsSubscription = this.router.events.subscribe(event => {
        if (event instanceof NavigationEnd) {
            this.lastSegment = event.url.split('?')[0].split('/')[4] || 'all';
        }
    });

    constructor(
        private router: Router,
        public config: I360Config,
    ) {}

    ngOnDestroy() {
        this.routerEventsSubscription.unsubscribe();
    }
}

class MenuItem {
    constructor(public route: string, public title: string, public disableCounts?: boolean) { }
}
