import {
    ComponentFactory, ComponentFactoryResolver, ComponentRef, Directive, EmbeddedViewRef,
    Input, OnChanges, OnDestroy, TemplateRef, ViewContainerRef,
} from '@angular/core';
import { SpinnerComponent } from 'app/utils/spinner/spinner.component';


@Directive({
    selector: '[i360SpinnerLoader]',
    exportAs: 'i360SpinnerLoader',
})
export class SpinnerLoaderDirective implements OnDestroy, OnChanges {
    @Input() set i360SpinnerLoader(show) {
        if (show) {
            this.show();
        } else {
            this.hide();
        }
    }
    @Input('i360SpinnerLoaderMessage') message: string;
    @Input('i360SpinnerLoaderColor') color: 'red' | 'green' | 'gray' = 'green';
    public isLoading: boolean = false;
    private factory: ComponentFactory<SpinnerComponent>;
    private ref: ComponentRef<SpinnerComponent>;
    private template: EmbeddedViewRef<any>;

    constructor(
        private container: ViewContainerRef,
        resolver: ComponentFactoryResolver,
        private templateRef: TemplateRef<any>,
    ) {
        this.factory = resolver.resolveComponentFactory(SpinnerComponent);
        this.template = this.container.createEmbeddedView(this.templateRef);
    }
    setFields() {
        this.ref.instance.show = true;
        this.ref.instance.color = this.color;
        this.ref.instance.message = this.message;
    }
    show(name?: any) {
        if (!this.isLoading) {
            this.template.destroy();
            this.ref = this.container.createComponent(this.factory);
            this.setFields();
            this.ref.changeDetectorRef.detectChanges();
            this.isLoading = true;
        }
    }
    hide(name?: any) {
        if (this.isLoading) {
            this.template = this.container.createEmbeddedView(this.templateRef);
            this.ngOnDestroy();
            this.isLoading = false;
        }
    }
    ngOnDestroy() {
        if (this.ref) {
            this.ref.destroy();
        }
    }

    ngOnChanges(): void {
        if (this.ref) {
            this.setFields();
        }
    }
}
