import { Injectable } from '@angular/core';
import { HttpHandler, HttpInterceptor, HttpRequest, HttpResponse } from '@angular/common/http';
import { Observable, tap } from 'rxjs';

import { AppState } from 'app/core/app.service';
import { postOnly } from './utils';

@Injectable()
export class StrategyInterceptor implements HttpInterceptor {
    constructor(
        private appState: AppState,
    ) {}

    @postOnly
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<any> {
        return next.handle(req).pipe(
            tap(event => {
                if (event instanceof HttpResponse) {
                    const strategy = event.body.data
                        ? event.body.data.strategy : event.body.strategy;
                    this.appState.csf = strategy === 'CSF_COOP';
                }
            }),
        );
    }
}
