import { Injectable } from '@angular/core';
import { unsupportedErrors, KernelcareAPI } from '@imunify360-api/kernelcare';
import { XHR } from 'app/utils/xhr/xhr';

@Injectable()
export class KernelcareService {
    constructor(public xhr: XHR) {}

    kcarectl = this.xhr.rx(KernelcareAPI.kcarectl, false, false);
    enableAutoUpdate = this.xhr.request(KernelcareAPI.enableAutoUpdate);
    disableAutoUpdate = this.xhr.request(KernelcareAPI.disableAutoUpdate);
    update = this.xhr.request(KernelcareAPI.update);
    /**
     * TODO: define type
     * FIXME: unreachable
     */
    patchInfo = this.xhr.rx<any, any>(KernelcareAPI.patchInfo);

    isUnsupported(error: string): boolean {
        return unsupportedErrors.includes(error);
    }
}
