import { Injectable } from '@angular/core';
import { BlockedRule, BlockedRulesAPI } from '@imunify360-api/blocked-rules';
import { XHR } from 'app/utils/xhr/xhr';


@Injectable()
export class BlockedRulesService {
    constructor(public xhr: XHR) {}

    enable = this.xhr.request(BlockedRulesAPI.enable);
    disable = this.xhr.request(BlockedRulesAPI.disable);
    edit = this.xhr.request(BlockedRulesAPI.disable);

    get = this.xhr.wrap(this.xhr.rx(BlockedRulesAPI.list), BlockedRule);
}
