import {
    AfterViewInit, ChangeDetectorRef,
    Component,
    ElementRef, Input,
    OnDestroy,
    ViewChild,
} from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { useLoader } from 'app/utils/loader/decorators';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { I360Validators } from 'app/utils/validator/validators';
import { TranslateService } from 'app/utils/translate/translate.service';
import { CountryService } from 'app/services/country';
import { AdvancedSearchComponent } from 'app/utils/table/widgets/advanced-search/advanced-search.component';

@Component({
    selector: 'i360-list-search-form',
    templateUrl: './list-search-form.component.html',
})
export class ListSearchFormComponent implements OnDestroy, AfterViewInit {
    @ViewChild(AdvancedSearchComponent, {static: false}) advancedSearch: AdvancedSearchComponent;
    @ViewChild('input', {read: ElementRef, static: true}) input: ElementRef<HTMLInputElement>;
    @Input() set validatorIP (validator) {
         this.form.controls.by_ip.setValidators([validator]);
    }
    private routeParamsSubscription = Subscription.EMPTY;
    private countriesLoaded = Subscription.EMPTY;
    public queryParams = {};
    form: UntypedFormGroup = this.formBuilder.group({
        by_ip: this.formBuilder.control('', [I360Validators.ipWithCIDR(false)]),
        by_comment: this.formBuilder.control('', []),
        by_country_code: this.formBuilder.control('',
            [],
            [this.countryService.validator(false)])
    });

    constructor(
        public formBuilder: UntypedFormBuilder,
        private route: ActivatedRoute,
        private translate: TranslateService,
        private countryService: CountryService,
        public router: Router,
        private cdRef : ChangeDetectorRef
    ) {}

    public ngAfterViewInit(): void {
        this.routeParamsSubscription = this.route.queryParams.subscribe(async params => {
            this.queryParams = JSON.parse(JSON.stringify(params));
            this.form.reset();
            this.form.patchValue(this.queryParams);
            this.countriesLoaded = this.countryService.loaded.subscribe(async (loaded) => {
                if (loaded) {
                    this.form.controls['by_country_code'].setValue(
                        await this.countryService
                            .translateCountryInUrl(this.queryParams['by_country_code'])
                    );
                }
            });
            this.advancedSearch.hasData = !!(this.queryParams['by_ip'] ||
                this.queryParams['by_comment'] || this.queryParams['by_country_code']);
            this.cdRef.detectChanges();
        });
    }

    public searchData() {
        let newParams = Object.assign(this.form.value, {
                ...this.queryParams,
                ...this.form.value});
        newParams['by_country_code'] =
            this.countryService.getCountryCode(newParams['by_country_code']);
        const url = this.router.url.split('?')[0];
        this.advancedSearch.cancel();
        this.router.navigate([url], {queryParams: newParams});
    }

    @useLoader
    async ngOnDestroy() {
        this.routeParamsSubscription.unsubscribe();
        this.countriesLoaded.unsubscribe();
    }
}
