import dayjs from 'dayjs';
import * as relativeTime from 'dayjs/plugin/relativeTime';
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import {
    EMPTY, Observable, Subject,
    catchError, takeUntil, tap, firstValueFrom,
} from 'rxjs';
import { useLoader } from 'app/utils/loader/decorators';
import { KernelcareService } from 'app/services/kernelcare';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { FeaturesService } from 'app/services/features';
import { KernelCareItem, KERNELCARE_STATUS_FETCHING } from '@imunify360-api/kernelcare';
import { AgentResponse, NormalizedAgentResponse } from '@imunify360-api/misc';
import { FeatureStatus } from '@imunify360-api/features';

dayjs.extend(relativeTime);

@Component({
    selector: 'kernelcare',
    styleUrls: ['./kernelcare.component.scss'],
    templateUrl: './kernelcare.component.html',
})
export class KernelcareComponent implements OnInit, OnDestroy {
    public status: FeatureStatus | typeof KERNELCARE_STATUS_FETCHING;
    public item: KernelCareItem;
    public showPatchInfo: boolean = false;
    public patchInfo: string = '';
    public destroyed = new Subject<void>();
    public FeatureStatus = FeatureStatus;
    public KERNELCARE_STATUS_FETCHING = KERNELCARE_STATUS_FETCHING;
    public error: string;

    @ViewChild(LoaderDirective, {static: false}) loader: LoaderDirective;

    statusRequest = this.featureService.getStatus(() => 'kernelcare').pipe(
        tap(res => {
            this.status = res.status;
            if (this.status === FeatureStatus.installed) {
                this.kcarectl();
            } else if (this.status === FeatureStatus.error) {
                this.error = res.message!;
            }
        }),
        takeUntil(this.destroyed),
    );

    constructor(
        public kernelcareService: KernelcareService,
        public featureService: FeaturesService,
    ) { }

    @useLoader
    public async updateModeToggle() {
        if (this.item.autoUpdate) {
            await this.kernelcareService.disableAutoUpdate();
        } else {
            await this.kernelcareService.enableAutoUpdate();
        }
        this.item.autoUpdate = !this.item.autoUpdate;
    }

    public async kcarectl() {
        this.status = KERNELCARE_STATUS_FETCHING;
        this.kernelcareService.kcarectl()
            .pipe(
                catchError(e => this.handleError(e)),
            ).subscribe(response => {
                this.item = new KernelCareItem(response.data.items);
                this.status = FeatureStatus.installed;
            });
    }

    @useLoader
    public async update() {
        await this.kernelcareService.update();
    }

    @useLoader
    async fetchPatchInfo() {
        // FIXME: unreachable code
        let response = await firstValueFrom(
            this.kernelcareService.patchInfo(), { defaultValue: { data: [] } }
        );
        this.patchInfo = response.data.join('<br />');
        this.showPatchInfo = true;
    }

    getUptime(): string {
        return dayjs.unix(new Date(((new Date()).getTime() / 1000) -
            (parseInt(this.item.uptime, 10))).valueOf(),
        ).fromNow(true);
    }

    ngOnInit() {
         this.statusRequest.subscribe();
    }

    ngOnDestroy() {
        this.destroyed.next();
    }

    private handleError(e: NormalizedAgentResponse<any>): Observable<AgentResponse> {
        this.status = FeatureStatus.error;
        this.error = e.messages.join(',');
        return EMPTY;
    }
}
