import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { AppState } from 'app/core/app.service';
import { Repeater } from 'app/utils/repeater';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { WidgetsOwnerDirective } from 'app/utils/table/request/misc';
import { Observable, firstValueFrom, map, Subject, takeUntil } from 'rxjs';
import { I360Config } from 'app/utils/config/config';


@Component({
    selector: 'i360-refresher',
    styleUrls: ['./refresher.component.scss'],
    templateUrl: './refresher.component.html',
})
export class RefresherComponent implements OnDestroy, OnInit {
    @Input() trackChanges: boolean = false;
    @Input() disabled: boolean = false;

    public contentChanged: boolean = false;
    public queryParams: any;
    private offset: number;
    private repeater: Repeater = new Repeater();
    private destroyed: boolean = false;
    private destroy$ = new Subject<void>();

    constructor(
        public appState: AppState,
        private config: I360Config,
        private request: RequestDirective,
        private widgetOwner: WidgetsOwnerDirective,
    ) { }

    public set enabled(value: boolean) {
        this.appState.refresherEnabled = value;
        this.start().catch();
    }

    public get enabled(): boolean {
        return this.appState.refresherEnabled;
    }

    public isContentChanged(): boolean {
        return this.trackChanges && this.offset > 0 && this.contentChanged;
    }

    public async start() {
        const refreshTime = await firstValueFrom(this.getRefreshTime());

        if (!refreshTime) {
            throw Error('i360-refresher refreshTime not set');
        }

        try{
            this.repeater.clear();

            if (!this.enabled || this.destroyed) {
                return;
            }

            await this.repeater.wait(refreshTime);
            if (!this.disabled) {
                await this.widgetOwner.load();
            }
            await this.start();
        } catch (e) { }
    }

    public ngOnDestroy() {
        this.repeater.clear();
        this.destroy$.next();
        this.destroyed = true;
    }

    public ngOnInit() {
        this.queryParams = {[`${this.request.name}offset`]: 0};
        let savedHash: string | undefined;
        this.request.loaded.pipe(takeUntil(this.destroy$))
            .subscribe((data) => {
                if (data.length) {
                    const hash = Object.keys(data[0]).sort().map(key => data[0][key]).toString();
                    if (savedHash !== void 0 && hash !== savedHash) {
                        this.contentChanged = true;
                    }
                    savedHash = hash;
                }
                this.start().catch();
            });
        this.request.changed.pipe(takeUntil(this.destroy$))
            .subscribe(event => {
                if (event.paramName === 'offset') {
                    this.offset = event.params.offset;
                }
                this.contentChanged = false;
                savedHash = void 0;
            });
    }

    private getRefreshTime(): Observable<number> {
        return this.config.changes.pipe(map(config => {
            return config.INCIDENT_LOGGING?.ui_autorefresh_timeout || 10;
        }));
    }
}
