import { AgentResponse, DataFrom, ListResponseData, RequestParams } from './misc';


export class BlockedPortsAPI {

    static list = (params = {}) =>
        new RequestParams<DataFrom<BlockedPortsAgentResponse>>(['blocked-port', 'list'], params)

    static addPort(port: BlockedPort) {
        return new RequestParams(['blocked-port', 'add'], {
            items: [port.requestValue()],
            ips: port.ips.map(ip => ip.toString()),
            comment: port.comment,
        });
    }

    static addIp({port, ip, comment}: {port: BlockedPort, ip: string, comment: string}) {
        return new RequestParams(['blocked-port-ip', 'add'], {
            items: [port.requestValue()],
            ips: [ip],
            comment,
        });
    }

    static deletePort(port: BlockedPort) {
        return new RequestParams(['blocked-port', 'delete'], {
            items: [port.requestValue()],
        });
    }
    static deletePorts(portProtos: string[]) {
        return new RequestParams(['blocked-port', 'delete'], {
            items: portProtos,
        });
    }
    static deleteIp({port, ip}: {port: BlockedPort, ip: string}) {
        return new RequestParams(['blocked-port-ip', 'delete'], {
            items: [port.requestValue()],
            ips: [ip],
        });
    }

    static editPortComment({port, comment}: {port: BlockedPort, comment: string}) {
        return new RequestParams(['blocked-port', 'edit'], {
            items: [port.requestValue()],
            comment,
        });
    }

    static editIpComment({port, ip, comment}: {port: BlockedPort, ip: string, comment: string}) {
        return new RequestParams(['blocked-port-ip', 'edit'], {
            items: [port.requestValue()],
            ips: [ip],
            comment,
        });
    }
}

interface BlockedPortInBackend {
    port: number;
    comment: string | null;
    proto: string;
    id?: number;
    ips: BlockedPortIp[];
}

export class BlockedPort {
    ips: BlockedPortIp[];
    port: number;
    comment: string | null;
    proto: string;
    canExpand: boolean;
    id: number;
    constructor({port, ips = [], comment = '', proto = 'all', id = 0}: BlockedPortInBackend) {
        this.id = id;
        this.port = port;
        this.proto = proto;
        this.ips = ips.map(ip => new BlockedPortIp(ip));
        this.comment = comment;
        this.canExpand = this.ips.length > 0;
    }
    toString() {
        return this.proto === 'all' ? this.port : this.requestValue();
    }
    requestValue() {
        return `${this.port}:${this.proto}`;
    }
}


export class BlockedPortIp {
    ip: string;
    comment: string | null;
    constructor({ip, comment = ''}: BlockedPortIp) {
        this.ip = ip;
        this.comment = comment;
    }
    toString() {
        return this.ip;
    }
}

// TODO: remove the count interface after the agent refactoring
export interface OldListCountsResponse<E> extends ListResponseData<E> {
    counts?: {
        'white': number,
        'gray': number,
        'black': number,
        'blocked-ports': number,
    };
}

interface BlockedPortsAgentResponse extends
    AgentResponse<OldListCountsResponse<BlockedPortInBackend>> {}
