import {
    Component, ElementRef, EventEmitter, Input, Output,
    ViewChild,
} from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { I360Validators } from 'app/utils/validator/validators';

@Component({
    selector: 'i360-textarea-edit',
    templateUrl: './textarea-edit.component.html',
    styleUrls: ['./textarea-edit.component.scss'],
})
export class TextareaEditComponent {
    @ViewChild('input', {static: true}) input: ElementRef;
    @Input() value: string[];
    @Input() default: string;
    @Output() save = new EventEmitter();

    editing: boolean = false;
    form: UntypedFormGroup;

    constructor(public element: ElementRef, private formBuilder: UntypedFormBuilder) {}

    begin() {
        this.form = this.formBuilder.group({
            domains: this.formBuilder.control(this.value,
                [I360Validators.commaSeparatedDomains]),
        });
    }

    done() {
        const {domains} = this.form.value;
        if (this.value === domains) {
            return;
        }
        this.save.emit(domains);
    }
}
