import { Directive, Input, OnDestroy } from '@angular/core';
import { of, timer, Subject, repeatWhen, switchMap, takeUntil, tap } from 'rxjs';
import { FileService } from 'app/services/file';
import { LogBoxComponent } from 'app/utils/log-box/log-box.component';


export const PERIOD_OF_LOGGING = 5000;

@Directive({
    selector: '[i360LogWatcher]',
    providers: [FileService], // do something with it
})
export class LogWatcherDirective implements OnDestroy {
    stop = new Subject<void>();
    @Input() set path(path: string) {
        this.stop.next();
        this.log.log = [];
        if (path) {
            of(null)
                .pipe(
                    switchMap(() => this.fileService.loadChunk(path)),
                    tap(chunk => this.log.log = this.log.log.concat([{text: chunk}])),
                    repeatWhen(notifications => { // start timer only after chunk is loaded, so
                        // retry interval is PERIOD_OF_LOGGING + LOAD_CHUNK_DURATION
                        return notifications.pipe(
                            switchMap(() => timer(PERIOD_OF_LOGGING)), // delay not working in tests
                        );
                    }),
                    takeUntil(this.stop),
                ).subscribe();
        }
    }

    constructor(private fileService: FileService, private log: LogBoxComponent) {
        this.fileService.notifyOnError = false; // nginx-captcha install returns bad file path
    }

    ngOnDestroy(): void {
        this.stop.next();
    }
}
