import { Component, OnDestroy } from '@angular/core';
import { BackupState } from 'app/components/settings/backup/backup-state';
import { Panel } from 'app/utils/panel';
import { NavigationEnd, Router } from '@angular/router';

@Component({
    selector: 'settings',
    templateUrl: './settings-index.component.html',
    styleUrls: [`./settings-index.component.scss`],
})
export class SettingsIndexComponent implements OnDestroy {
    public shouldShowBackupStuff = false;
    private routerEventsSubscription = this.router.events.subscribe(event => {
        if (event instanceof NavigationEnd) {
            let url = event.url.split('/');
            this.shouldShowBackupStuff = url[url.length - 1] === 'backup';
        }
    });
    constructor(
        public backupState: BackupState,
        public panel: Panel,
        public router: Router,
    ) {

    }
    ngOnDestroy() {
        this.routerEventsSubscription.unsubscribe();
    }
}
