import {
    ChangeDetectionStrategy,
    Component, EventEmitter, Input, NgZone, Output, ViewChild, Renderer2,
} from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';

@Component({
    selector: 'i360-inline-edit',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './inline-edit.component.html',
    styleUrls: ['./inline-edit.component.scss'],
})
export class InlineEditComponent {
    @ViewChild('stub', {static: false}) stub;
    @Input() text: string;
    @Input() validation = [];
    @Output() save = new EventEmitter();

    form: UntypedFormGroup;

    constructor(
        private zone: NgZone,
        private formBuilder: UntypedFormBuilder,
        private renderer: Renderer2,
    ) {}

    begin() {
        this.form = this.formBuilder.group({
            raw: this.formBuilder.control(this.text, this.validation),
        });
    }

    resize() {
        this.zone.runOutsideAngular(() => {
            const sub = this.zone.onStable.subscribe(() => {
                sub.unsubscribe();
                let stub = this.stub.nativeElement;
                this.renderer.setStyle(stub.parentElement, 'width', `${stub.clientWidth + 75}px`);
            });
        });
    }

    done() {
        const raw = this.form.value.raw;
        if (this.text === raw || !this.form.valid) {
            return;
        }
        this.save.emit(raw);
    }
}
