import { Injectable } from '@angular/core';
import { HttpHandler, HttpInterceptor, HttpRequest, HttpResponse } from '@angular/common/http';
import { Observable, tap } from 'rxjs';

import { postOnly } from './utils';

export interface FirewallCounts {
    cloud: {
        white: number,
        captcha: number,
        drop: number,
        splashscreen: number,
    },
    server: {
        white: number,
        captcha: number,
        drop: number,
        splashscreen: number,
    }
}

@Injectable()
export class FirewallCountsInterceptor implements HttpInterceptor {
    counts: FirewallCounts;

    @postOnly
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<any> {
        return next.handle(req).pipe(
            tap((event) => {
                if (event instanceof HttpResponse && event.body.data) {
                    const counts = event.body.data.counts;
                    if (counts) {
                        this.counts = counts;
                    }
                }
            }),
        );
    }
}
